import React, { useEffect, useRef, useState } from "react";
import VideoList from "./video_list/video_list";
import styles from "./keyword.module.css";
import SearchAnalysis from "./search_analysis/searchAnalysis";

function Keyword({ onSearch }) {
  const [videos, setVideos] = useState([]);
  const [results, setResults] = useState([]);
  // Get Week of Year
  const now = new Date();
  const thisYear = now.getFullYear();
  const onejan = new Date(now.getFullYear(), 0, 1);
  const weekNumber =
    Math.ceil(
      ((now.getTime() - onejan.getTime()) / 86400000 + onejan.getDay() + 1) / 7
    ) - 1;
  //

  // Get Day of Year
  const start = new Date(now.getFullYear(), 0, 0);
  const diff =
    now -
    start +
    (start.getTimezoneOffset() - now.getTimezoneOffset()) * 60 * 1000;
  const oneDay = 1000 * 60 * 60 * 24;
  const day = Math.floor(diff / oneDay);
  // console.log("Day of year: " + day);

 const search = (keyword) => {
   // console.log(keyword);
  
   let result = [];
   let filteredResult = [];
   let searchResult = [];

   try {
     result = require("../collected.json");
   } catch (error) {
     console.log(error);
   }

   for (const video of result) {
     const title = video["title"];
     if (
       title.search(keyword) >= 0 //
     ) {
       // console.log(title)
       searchResult.push(video);
       if (video.viewCount > 100000) {
         filteredResult.push(video);
       }
     }
   }
   setResults(searchResult);
   setVideos(filteredResult);
 };

  const inputRef = useRef();
  
  const handleSearch = (event) => {
    event.preventDefault();
    // console.log("submitted")
    const value = inputRef.current.value;
    value && search(value);
  };
  const onClick = (event) => {
    handleSearch(event);
  };

  const onKeyPress = (event) => {
    if (event.key === "Enter") {
      handleSearch(event);
    }
  };


  
  return (
    <>
      <form className={styles.searchForm} onSubmit={handleSearch}>
        <input
          ref={inputRef}
          className={styles.searchInput}
          type="search"
          placeholder="검색어"
          onKeyPress={onKeyPress}
        />
        <button className={styles.searchButton} type="submit" onClick={onClick}>
          검색
        </button>
      </form>
      {/* <p className={styles.meta}>키워드를 검색해보세요</p> */}
      {results.length >0 && <SearchAnalysis results={results} videos={videos}/>}
      <VideoList videos={videos} />
    </>
  );
}

export default Keyword;
