import React from "react";
import styles from "./wordclouds.module.css";

function Wordclouds(props) {
  const arr = [
    "01",
    "02",
    "03",
    "04",
    "05",
    "06",
    "07",
    "08",
    "09",
    "10",
    "11",
    "12",
  ];
  return (
    <div className={styles.cloudDiv}>
      {arr.map((month) => {
        return (
          <div className={styles.cloudItem}>
            <span className={styles.month}>{month}월</span>
            {/* <div className={styles.imgContainer}></div> */}
            <img
              className={styles.cloudImage}
              src={require(`../wordclouds/cloud_${month}.png`)}
              alt={month}
            />
          </div>
        );
      })}
    </div>
  );
}

export default Wordclouds;
